@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@200;300;400;500;600;700&display=swap);
/* Roboto */

/* Oswald */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Roboto", sans-serif;
}

.filter-cnt {
  width: -webkit-max-content;
  width: max-content;
  margin: 0 auto;
}

.filter-cnt {
  margin-top: 2rem;
}

.GenderFilterBtn:nth-child(n+2) {
  margin-left: 1.5rem;
}
.Header {
    background: #8B008B;
    font-family: "Oswald", sans-serif;
    color: #fff;
    height: 15vh;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.Header h1 {
    text-align: center;
}
.GenderFilterBtn {
    font-size: 2.5rem;
    color: #fff;
    border: none;
    background: rgba(139, 0, 139, 0.7);
    padding: 0.5rem 1.25rem;
    border-radius: 50px;
    cursor: pointer;
}
.AlphabeticalSortBtn {
    font-size: 1.3rem;
    width: -webkit-max-content;
    width: max-content;
    margin: 0 auto;
    margin-top: 2rem;
    display: block;
    background: #000;
    border: solid 4px #000;
    padding: 0.5rem 1rem;
    color: #fff;
    cursor: pointer;
}
.EmployeeTable {
    margin: 0 auto;
    margin-top: 1.5rem;
    max-width: 900px;
}
.EmployeeRow {
    background: #8B008B;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    color: #fff;
    font-size: 1.2rem;
    padding: 1rem 0;
    position: relative;
    min-height: 100px;
}

.EmployeeRow img {
    position: absolute;
    left: 3rem;
}

.EmployeeRow .employee-cell {
    position: absolute;
    right: 3rem;
}

.EmployeeRow:nth-child(n+2) {
    margin-top: 1rem;
}
